import './Aboutme.scss';
import { BsCaretDownFill } from "react-icons/bs";
import { Link } from 'react-scroll';


export default function Aboutme() {
  return (
    <section id='aboutme'>
      <div className='container aboutmeWrap'>
        <h1 className='h1'>.<br />
            .<br />
        </h1>
        <Link
          to='project'
          spy={true}
          smooth={true}
          offset={-70}
          duration={300}
        >
          <button className='moreBtn'>
              <BsCaretDownFill />
          </button>
        </Link>
      </div>
    </section>
  )
}